import React from "react";
import Notification from "../MainDashboard/Notification";
import { connect } from "react-redux";
import { compose } from "redux";
import { firestoreConnect } from "react-redux-firebase";
import { Helmet } from "react-helmet";
function MainDashboard({ notification }) {
  return (
    <>
      <Helmet>
        <title>OwlTruck</title>
      </Helmet>
      <div className="container-fluid">
        <div className="row">
          <div className="col-9">
            <h1 style={{ color: "black" }} className="display-4-left">
              Introduction
            </h1>
            <p style={{ color: "black" }} className="lead">
              <b>
                <strong style={{ fontSize: "25px" }}>OwlTruck</strong> is a
                platform which helps both carrier and shipper to meet each other
                and give lots of benefits to them . It solves the empty
                container problem in truck industry.
              </b>
            </p>

            <div className="container-fluid">
              <div className="row">
                <div className="col">
                  <h4 style={{ color: "black" }} className="h4">
                    Carrier
                  </h4>

                  <p style={{ color: "black" }} className="lead">
                    <b>
                      Carrier are those person who take item for delivery, Like
                      Truck Driver.
                      <br />
                      <br />
                      <h5 className="h5">Benefits of carrier</h5>
                      <strong>*</strong> Gives new customer and business <br />
                      <strong>*</strong> Can check shippment coming in thier
                      route <br />
                      <strong>*</strong>Can earn more by providing extra item
                      delivery
                    </b>
                  </p>
                </div>
                <div className="col">
                  <h4 style={{ color: "black" }} className="h4">
                    Shipper
                  </h4>
                  <p style={{ color: "black" }} className="lead">
                    <b>
                      Shipper are those person who give their item for delivery
                      <br />
                      <br />
                      <h5 className="h5">Benefits of shipper</h5>
                      <strong>*</strong> Provides trust to shipper with verified
                      carrier only
                      <br /> <strong>*</strong> Most important saves money in
                      each shippment
                      <br />
                      <strong>*</strong>
                      They don't need to book whole carrier for small shippments
                    </b>
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="col-3">
            <br />
            <h3 className="h3">Notifications</h3>
            <Notification notification={notification} />
          </div>
        </div>
      </div>
    </>
  );
}
const mapStateToProps = (state) => {
  return {
    notification: state.firestore.ordered.notification,
  };
};
export default compose(
  connect(mapStateToProps),
  firestoreConnect([
    { collection: "notification", limit: 5, orderBy: ["time", "desc"] },
  ])
)(MainDashboard);
